import axios from 'axios'
import { url } from '../../helper/env'

const actions = {
  onDelete (context, payload) {
    return new Promise((resolve, reject) => {
      axios.delete(`${url}/message/delete/${payload}`).then((response) => {
        resolve(response)
      }).catch((err) => reject(err))
    })
  }
}

export default {
  namespaced: true,
  actions
}
