<template>
  <div class="about">
    <div class="row">
      <!-- Sidebar -->
      <div class="sidebar-page col-md-4 d-none d-md-block">
        <div class="container mt-2">
          <b-row>
            <b-col>
              <router-link to="/home">
              <h4 class="font-weight-bold text-violet mt-2">Arkachat</h4>
              </router-link>
            </b-col>
            <b-col class="icon-nav text-right text-violet">
              <b-dropdown variant="violet">
                <template v-slot:button-content>
                  <b-icon-justify-left class="icon"></b-icon-justify-left>
                </template>
                <b-dropdown-item to="/profile"><b-icon-gear class="mr-3"></b-icon-gear> Setting</b-dropdown-item>
                <b-dropdown-item><b-icon-person class="mr-3"></b-icon-person> Contacts</b-dropdown-item>
                <b-dropdown-item><b-icon-telephone class="mr-3"></b-icon-telephone> Calls</b-dropdown-item>
                <b-dropdown-item><b-icon-bookmark class="mr-3"></b-icon-bookmark> Save messages</b-dropdown-item>
                <b-dropdown-item><b-icon-person-plus class="mr-3"></b-icon-person-plus> Invite Friends</b-dropdown-item>
                <b-dropdown-item><b-icon-question-circle class="mr-3"></b-icon-question-circle> ArkaChat FAQ</b-dropdown-item>
                <b-dropdown-item @click="logout"><b-icon-box-arrow-right class="mr-3"></b-icon-box-arrow-right> Logout</b-dropdown-item>
              </b-dropdown>
            </b-col>
          </b-row>
          <form >
            <div class="form">
              <button><b-icon icon="search" variant="secondary"></b-icon></button>
              <input type="text" class="form-control" placeholder="Search here" v-model="keyword" />
            </div>
            <div class="row">
              <a class="btn btn-sort"> All </a>
              <a class="btn btn-sort"> Important </a>
              <a class="btn btn-sort"> Unread </a>
            </div>
          </form>
          <hr>
          <div class="list-contact">
            <div v-for="(item, index) in listUsers" :key="index">
              <div v-if="username !== item.username">
                <div class="contact-person form-inline" @click="selectUser(item.username), selectImage(item.image), selectId(item.id)">
                  <img :src="`${url}/${item.image}`" alt="photo" class="pl-2 image-person">
                    <div class="info ml-3 pt-2">
                      <b>{{item.username}}</b>
                      <p class="small text-violet mt-2">{{ item.message }}</p>
                    </div>
                  </div>
                  <hr>
                </div>
              </div>
            </div>
          </div>
      </div>

      <!-- Sidebar responsive -->
      <div class="sidebar-page col-sm-12 d-block d-md-none">
        <b-row class="container-fluid mt-3">
        <b-col>
          <router-link to="/home">
          <h4 class="font-weight-bold text-violet ml-3 mt-2">Arkachat</h4>
          </router-link>
        </b-col>
        <b-col class="icon-nav text-right text-violet">
          <b-dropdown variant="violet">
            <template v-slot:button-content>
              <b-icon-justify-left class="icon"></b-icon-justify-left>
            </template>
            <b-dropdown-item to="/profile"><b-icon-gear class="mr-3"></b-icon-gear> Setting</b-dropdown-item>
            <b-dropdown-item><b-icon-person class="mr-3"></b-icon-person> Contacts</b-dropdown-item>
            <b-dropdown-item><b-icon-telephone class="mr-3"></b-icon-telephone> Calls</b-dropdown-item>
            <b-dropdown-item><b-icon-bookmark class="mr-3"></b-icon-bookmark> Save messages</b-dropdown-item>
            <b-dropdown-item><b-icon-person-plus class="mr-3"></b-icon-person-plus> Invite Friends</b-dropdown-item>
            <b-dropdown-item><b-icon-question-circle class="mr-3"></b-icon-question-circle> ArkaChat FAQ</b-dropdown-item>
            <b-dropdown-item @click="logout"><b-icon-box-arrow-right class="mr-3"></b-icon-box-arrow-right> Logout</b-dropdown-item>
          </b-dropdown>
        </b-col>
      </b-row>
      </div>

      <div class="d-block d-md-none">
        <b-button v-b-toggle.chat class="connect" variant="light">
          <b-icon-chat-dots-fill class="connect" ></b-icon-chat-dots-fill>
        </b-button>
        <b-sidebar id="chat" shadow>
          <div class="px-3 py-2">
            <form >
              <div class="form">
                <button><b-icon icon="search" variant="secondary"></b-icon></button>
                <input type="text" class="form-control" placeholder="Search here" v-model="keyword" />
              </div>
              <div class="row">
                <a class="btn btn-sort"> All </a>
                <a class="btn btn-sort"> Important </a>
                <a class="btn btn-sort"> Unread </a>
              </div>
            </form>
            <hr>
            <div class="list-contact">
              <div v-for="(item, index) in listUsers" :key="index">
                <div v-if="username !== item.username">
                  <div class="contact-person form-inline" @click="selectUser(item.username), selectImage(item.image), selectId(item.id)">
                    <img :src="`${url}/${item.image}`" alt="photo" class="pl-2 image-person">
                      <div class="info ml-3 pt-2">
                        <b>{{item.username}}</b>
                        <p class="small text-violet mt-2">{{ item.message }}</p>
                      </div>
                  </div>
                  <hr>
                </div>
              </div>
            </div>
          </div>
        </b-sidebar>
      </div>

    <!-- Chat Content -->
      <div class="col-md-8">
        <div class="chat-page">
          <div v-if="userReceiver === null || userReceiverImage === null">
            <p class="text-muted text-empty">Plese select a chat to start messaging</p>
          </div>
          <div v-else>
            <div class="header-info form-inline" v-b-toggle.friend-info>
              <FriendDetail />
              <img :src="`${url}/${userReceiverImage}`" alt="photo" class="m-2 image-person">
              <div class="info ml-3">
                <b>{{userReceiver}}</b>
                <div v-if="typing === userReceiver">
                  <p class="small text-violet">{{typing}} is typing</p>
                </div>
                <div v-else-if="detailUser.refreshToken !== null">
                  <p class="small text-violet">online</p>
                </div>
              </div>
            </div>
            <div class="chat-content">
              <div v-for="(item, index) in historyMessages" :key="index">
                <p v-if="item.sender === username" class="bg-lightblue text-white ml-auto chat-in p-2 m-3 " style="max-width: 100%;" @click="alertDelete(item.id)">{{item.message}}</p>
                <p v-else class="chat-out p-2 m-3 " style="max-width: 100%;">{{item.message}}</p>
              </div>
              <div v-for="(item, index) in listMessage" :key="index">
                <p v-if="item.sender === username" class="bg-lightblue text-white ml-auto chat-in p-2 m-3 " style="max-width: 100%;" @click="alertDelete(item.id)">{{item.message}}</p>
                <p v-else class="chat-out p-2 m-3 " style="max-width: 100%;">{{item.message}}</p>
              </div>
            </div>
            <form @submit.prevent="sendMessage()">
              <div class="form mt-3 ml-4 mr-4">
                <input type="text" class="form-control" placeholder="Type message here ..." v-model="message">
                <button><b-icon icon="plus" variant="secondary"></b-icon></button>
                <button><b-icon icon="emoji-smile" variant="secondary"></b-icon></button>
                <button><b-icon icon="file-image" variant="secondary"></b-icon></button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swal from 'sweetalert2'
import io from 'socket.io-client'
import { url } from '../helper/env'
import { mapGetters, mapActions } from 'vuex'
import FriendDetail from '../components/FriendDetail'

export default {
  name: 'Home',
  data () {
    return {
      url,
      id: localStorage.getItem('id'),
      username: localStorage.getItem('username'),
      idreceiver: this.$route.query.iduser,
      socket: io(`${url}`),
      typing: false,
      connections: false,
      keyword: null,
      message: '',
      userReceiver: null,
      userReceiverImage: null,
      listUsers: [],
      listMessage: [],
      privateMessages: [],
      historyMessages: []
    }
  },
  components: {
    FriendDetail
  },
  watch: {
    message (value) {
      value ? this.socket.emit('typing', this.username) : this.socket.emit('stopTyping')
    }
  },
  computed: {
    ...mapGetters({
      isLogin: 'auth/isLogin',
      detailUser: 'user/getDetail'
    })
  },
  methods: {
    ...mapActions({
      onLogout: 'auth/onLogout',
      onDelete: 'message/onDelete',
      onDetail: 'user/onDetail'
    }),
    logout () {
      this.onLogout(this.id).then((result) => { window.location = '/' })
    },
    alertDelete (id) {
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
        if (result.isConfirmed) {
          this.onDelete(id)
          location.reload()
          Swal.fire(
            'Deleted!',
            'Your message has been deleted.',
            'success'
          )
        }
      })
    },
    selectUser (user) {
      this.listMessage = []
      this.privateMessages = []
      this.userReceiver = user
      this.getPrivateMessage()

      this.socket.emit('get-history-message', {
        sender: this.username,
        receiver: user
      })
      this.getHistoryMessage()
    },
    selectImage (image) {
      this.userReceiverImage = image
    },
    selectId (iduser) {
      this.$router.push({ path: '/home', query: { iduser } })
      this.onDetail(this.idreceiver)
    },
    getPrivateMessage () {
      const privateMessage = this.listMessage.filter((e) => {
        if (this.userReceiver === null) {
          return e.sender === this.userReceiver || e.sender === this.username
        } else {
          return e.userReceiver === this.userReceiver || e.sender === this.userReceiver
        }
      })
      this.privateMessages = privateMessage
    },
    getHistoryMessage () {
      this.socket.on('history-list-messages', (data) => {
        this.historyMessages = data
      })
    },
    sendMessage () {
      const msg = `${this.message}`
      this.listMessage = [...this.listMessage, {
        sender: this.username,
        receiver: this.userReceiver,
        message: msg
      }]

      this.getPrivateMessage()

      this.socket.emit('send-message', {
        sender: this.username,
        receiver: this.userReceiver,
        message: msg
      })
      this.message = ''
    }
  },
  mounted () {
    this.socket.emit('get-all-users', (this.id))
    this.socket.on('list-users', (data) => {
      this.listUsers = data
    })
    this.socket.emit('join-room', {
      user: this.username
    })
    this.socket.on('list-message', (data) => {
      this.listMessage = [...this.listMessage, data]
      if (this.userReceiver !== null) {
        this.getPrivateMessage()
      }
    })
    this.getHistoryMessage()
    this.socket.on('typing', (username) => {
      this.typing = username
    })
    this.socket.on('stopTyping', () => {
      this.typing = false
    })
    this.socket.on('connections', (data) => {
      this.connections = data
    })
  }
}
</script>

<style scoped>
.about {
  height: 100vh;
  background-color: #FAFAFA;
  overflow: hidden;
}
.text-empty {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.chat-page {
  margin-left: -15px;
  height: 100vh;
}
.connect {
  position: fixed;
  right: 20px;
  bottom: 60px;
  z-index: 1;
  color: #7E98DF;
  font-size: 20px;
}
.connect:focus {
  color: #7E98DF;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.header-info {
  padding: 5px;
  background-color: #fff;
}
.text-violet {
  color:  #7E98DF;
}
.chat-content {
  height: 480px;
  overflow-y: scroll;
  scroll-behavior: smooth;
  border-bottom: 1px solid #ddd;
  background-color:#F6F6F6;
}
::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
input[type="text"] {
  background-color: transparent;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  position: relative;
  bottom: 0;
}
input[type="text"]:focus {
  background-color: transparent;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.form {
  display: flex;
  background-color: #f3efef;
  border-radius: 20px;
  margin: 20px;
  position: relative;
  bottom: 0;
}
.form button {
  border: none;
  outline: none;
  background-color: transparent;
}
.bg-lightblue {
  background-color: #7E98DF;
  color: #fff;
}
.chat-in {
  width: fit-content;
  justify-content: end;
  border-radius: 10px 10px 0px 10px;
}
.chat-out {
  border: 1px #7E98DF solid;
  width: fit-content;
  border-radius: 0px 10px 10px 10px;
}

/* Sidebar */
.sidebar-page {
  background-color: #fff;
  border: 1px solid #ddd;
}
.form {
  display: flex;
  background-color: #f3efef;
  border-radius: 20px;
}
.form button {
  border: none;
  outline: none;
  background-color: transparent;
}
input[type="text"] {
  background: transparent;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}
input[type="text"]:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
  background: transparent;
}
.icon {
  width: 40px;
  height: 40px;
  margin-top: -6px;
  color: #7E98DF;
}
.text-violet {
  color:  #7E98DF;
}
.btn-sort {
  margin: auto;
}
.btn-sort:active, .btn-sort:hover {
  background-color:  #7E98DF;
  color: #fff !important;
  border-radius: 15px;
}
.btn-violet {
  -webkit-box-shadow: none;
  box-shadow: none;
  border: none;
}
.btn-violet:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
  border: none;
}
.dropdown-violet {
  background-color: #7E98DF;
}
.list-contact {
  height: 450px;
  overflow-y: scroll;
}
.contact-person {
  cursor: pointer;
}
.image-person {
  height: 60px;
  border-radius: 20px;
}
.icon-nav {
  margin-right: -15px;
}
@media(max-width: 1093px) {
  .chat-page {
    height: 100%;
  }
  .chat-content {
    height: 420px;
  }
}
@media(max-width: 768px) {
  .chat-page {
    margin-left: 3px;
    height: 100%;
  }
  .chat-page p {
    margin-top: 30%;
  }
  .chat-content {
    height: 350px;
  }
}
@media(max-width: 576px) {
  .chat-page {
    margin-left: 3px;
    height: 100%;
  }
  .chat-page p {
    margin-top: 50%;
  }
  .chat-content {
    height: 350px;
  }
  .icon-nav {
    margin-right: -15px;
  }
}
</style>
